@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeout {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

.fadeout {
  animation: fadeout 1s forwards !important;
}

.App {
  animation: fadein 2s;
  /* position: fixed; */
  overflow-x: hidden;
}

body {
  transition: 1s;
  /* overflow: hidden; */
}
