.switch {
  height: 50px;
  width: 50px;
  border-radius: 50%;
  z-index: 1000;
  position: fixed;
  bottom: 5vh;
  transition: 1s;
}

.darkMode {
  color: white;
  background-color: black;
  background-image: url(./media/darkmode.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: 0px 0px 58px -21px rgba(0, 0, 0, 0.75);
}

.lightMode {
  color: black;
  background-color: white;
  background-image: url(./media/lightmode.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  box-shadow: 0px 0px 58px -5px rgba(0, 0, 0, 0.75);
}

